////
/// @group o-forms
/// @link http://registry.origami.ft.com/components/o-forms
////

/// Silent mode
/// @type Bool
$o-forms-is-silent: true !default;

/// Field font family
///
/// This is required to override default system form element styling,
/// since `<input>`, `<textarea>`, `<select>`â€¦ don't inherit global font styles
///
/// @type String
$o-forms-font-family: BentonSans, sans-serif !default;

$_o-forms-fieldborder-width: 1px;
$_o-forms-fieldborder: $_o-forms-fieldborder-width solid oColorsGetColorFor(o-forms-field-standard, border);
$_o-forms-field-borderradius: 5px;

$_o-forms-field-small-fontsize: 14px;
$_o-forms-field-default-fontsize: 16px;

$_o-forms-field-small-padding-topbottom: 3px;
$_o-forms-field-default-padding-leftright: 5px;
$_o-forms-field-default-padding-top: 7px;
$_o-forms-field-default-padding-bottom: 8px;
$_o-forms-field-default-height: 36px;
$_o-forms-field-small-height: 24px;

$_o-forms-group-padding: oGridGutter();

// Outer circle / box sizes
$_o-forms-radiocheckbox-small-size: 17px;
$_o-forms-radiocheckbox-default-size: 24px;

// Inner circle sizes
$_o-forms-radio-internal-small-size: 7px;
$_o-forms-radio-internal-default-size: 12px;

$_o-forms-label-marginbottom: 5px;

$_o-forms-select-iconsize: 19px;
$_o-forms-select-small-iconsize: 16px;
